<template>
  <a-modal 
    :maskClosable="false" 
    :title="$t('common.detail')" 
    :width="1000" 
    v-model="isShow"
    @ok="onSubmit"
    @cancel="cancel" 
    :destroyOnClose="true">
    <template slot="footer">
      <a-button key="back" @click="cancel" >{{ this.$t('common.btn_cancel') }}</a-button>
    </template>
    <div class="table-page-search-wrapper">
      <s-table  
        ref="table"
        :bordered="false"
        :autoLoad="true"
        :data="loadData"
        size="small"
        rowKey="id"
      >
        <a-table-column 
          :title="this.$t('task.index1')"
          width="50px"
          align="center"
        >
          <span slot-scope="text">
            <a-icon type="schedule" theme="twoTone" class="icon" style="font-size:25px"/>
          </span>
        </a-table-column>
        <a-table-column
          :title="this.$t('dataedit.index9')"
          width="150px"
          dataIndex="id"
        >
        </a-table-column>
        <a-table-column
          :title="this.$t('task.description')"
          width="250px"
          dataIndex="name"
        >
          <span slot-scope="text,record">
            <span>{{ $t('vee.'+record.id+'_L') }}</span>
          </span>
        </a-table-column>
        <a-table-column
          :title="this.$t('dataedit.index10')"
          width="150px"
          dataIndex="result"
        >
          <span slot-scope="text,record">
            <span>{{ $t('veelog.result'+record.result) }}</span>
          </span>
        </a-table-column>
        <a-table-column
          :title="this.$t('dataedit.index11')"
          width="250px"
          dataIndex="remarks"
        >
        </a-table-column>
      </s-table>
    </div>
  </a-modal>
</template>

<script>
  import STable from '@/components/table/'
  import {
    axios
  } from '@/utils/request'
  export default {
    components: {
      STable,
    },
    data() {
      return {
        isShow: false,
        loadData:  parameter => { 
          const that = this
          parameter.id = that.params.id
          return axios({
            url: '/vee/dataedit/search-fail-detail',
            method: 'get',
            params: Object.assign('', parameter)
          }).then(res => {           
            return res.result
          })
        },
      }
    },
    methods: {
      showModal(params) {
        this.isShow = true
        this.params=params
      },
      onSubmit () {        
        this.isShow = false
      },
      cancel() {
        this.isShow = false
      },
    },
  }
</script>
