<template>
  <a-modal 
    :maskClosable="false" 
    :title="$t('dataedit.title1')" 
    :width="800" 
    v-model="isShow"
    @ok="onSubmit"
    @cancel="cancel" 
    :destroyOnClose="true">
    <a-form layout="inline">
      <a-row :gutter="16">
        <a-col :xl="{span:7}" :lg="{span:7}" :md="12" :sm="24">
          <a-form-item :label="this.$t('dataedit.index7')">
            <span>{{ data.refCode }}</span>
          </a-form-item>
        </a-col>
        <a-col :xl="{span:7}" :lg="{span:7}" :md="12" :sm="24">
          <a-form-item :label="this.$t('dataedit.index8')" >
            <span>{{ data.deviceNum }}</span>
          </a-form-item>
        </a-col>
        <a-col :xl="{span:7}" :lg="{span:7}" :md="12" :sm="24">
          <a-form-item :label="this.$t('common.fail_reason')" >
            <span v-if="data.veeType==0&&data.veeResult==1">{{ $t('dataedit.result_type0') }}</span>
            <span v-if="data.veeType==0&&data.veeResult==2">{{ $t('dataedit.result_type1') }}</span>
            <span v-if="data.veeType==1&&data.veeResult==2">{{ $t('dataedit.result_type2') }}</span>
            <span v-if="data.veeType==1&&data.veeResult==0">{{ $t('dataedit.result_type3') }}</span>
            <span v-if="data.veeType==2&&data.veeResult==0">{{ $t('dataedit.result_type4') }}</span>
          </a-form-item>
        </a-col>
      </a-row>
    </a-form>
    <a-row :gutter="16" style="background:#1890FF;height:30px;line-height:30px;color:white;margin-top:10px;text-align: center;">
      <a-col :span="5">{{ $t('vee.files') }}</a-col>
      <a-col :span="5">{{ $t('dataedit.date') }}</a-col>
      <a-col :span="5">{{ $t('dataedit.index5') }}</a-col>
      <a-col :span="5">{{ $t('dataedit.index6') }}</a-col>
    </a-row>
    <a-row :gutter="16" style="text-align: center;margin-top:10px;padding-bottom:20px;line-height:32px">
      <a-col :span="5">{{ data.fieldId }}</a-col>
      <a-col :span="5"><pick-label :defaultValue="data.readTime" /></a-col>
      <a-col :span="5">{{ data.realValue }}</a-col>
      <a-col :span="5">
        <a-input v-model="editValue" />
      </a-col>
    </a-row>
  </a-modal>
</template>

<script>
import { axios } from '@/utils/request'
  export default {
    components: {
     
    },
    data() {
      return {
        data: {},
        queryParam: {},
        params:'',
        editValue:'',
        isShow:false,
      }
    },
    mounted(){
    },
    methods: {
      showModal(record){
        this.data=record
        this.editValue=record.editValue?record.editValue:''
        this.isShow=true
      },
       onSubmit () {  
        axios({
          url: '/vee/dataedit/update',
          method: 'post',
          params: {id:this.data,editValue:this.editValue}
        }).then(result=>{
          if(result){
            this.$emit('ok',result)
            this.isShow = false
          }
        })   
      },
      cancel() {
        this.isShow = false
      },
    },
  }
</script>
<style lang="scss" scoped>
.selected{
    background: #ddd;
  }
  .item{
    
    padding: 5px 10px;
  
  }
  .cspan{
    display: inline-block;
  
  }
</style>