<template>
  <div>
    <a-select 
      style="width: 100%;"
      v-model="currValue"
      :placeholder="$t('dict.pbl_select')" 
      :mode="mode"
      @change="onChange"
      :disabled="disabled"
      :size="size"
      :allowClear="allowClear"
    >
      <a-select-option v-for="(text, key) in orgType" :key="key" :value="key">{{ getLabel(key, 'html') }}</a-select-option>                
    </a-select>
  </div>
</template>
<script>
import { axios } from '@/utils/request'
export default {
  data () {
    return {
      orgType: [],
      currValue: [],
    }
  },
  props: {
    isPublic: {
      type: Boolean,
      default: false
    },
    mode: {
      type: String,
      default: 'default'
    },
    disabled:{
      type: Boolean,
      default: false
    },
    type: {
      type: String,
      required: true
    },
    size: {
      type: String,
      default: 'default'
    },
    value: {
      type: Array,
      default: null
    },
    all: {
      type: Boolean,
      default: false
    },
    allowClear: {
      type: Boolean,
      default: true
    },
  },
  watch: {
    value(value){
      value&&value[0]?this.currValue = value:null
    }
  },
  created(){
    const that = this
    axios({
      method: 'get',
      url: '/sys/dictionary/public-dict-combo',
      params: {tableType: that.type, isPublic: that.isPublic}
    }).then(function (res) { 
      if(that.all){
        that.orgType = Object.assign({'':'All'},res.result)
      }else{
        that.orgType = res.result
      }
    })
  },
  methods: {
    onChange(value){
      if(typeof value=='string'){
        this.$emit('change',[value])
      }
      else{
        this.$emit('change',value)
      }
    },
    getLabel(key, format){
      if(this.orgType[key]){
        let label = this.orgType[key]
        let level = 0
        if(typeof label == 'object'){
          level = label.level
          label = label.text
        }
        if(this.$t('dct.' + this.type + key) != 'dct.' + this.type + key){
          label = this.$t('dct.' + this.type + key)
        }
        if(format == 'html'){
          if(level > 0){
            label = '|--' + label
          }
          while(level-- > 0){
            label = '\u3000' + label
          }
        }
        return label
      }
      else{
        return ''
      }
    },
    getAll() {
      const filterData = []
      for(const key in this.orgType) {
        const item  = {}
        let label = this.orgType[key]
        if(typeof label == 'object'){
          label = label.text
        }
        item['text'] = (this.$t('dct.' + this.type + key) != 'dct.' + this.type + key)?this.$t('dct.' + this.type + key):label
        item['value'] = key
        filterData.push(item)
      }
      return filterData
    },
    getValues(){
      const that = this
      if(that.orgType.length>0){
        return new Promise(function(resolve) {
          resolve(that.orgType)
        })
      }
      return new Promise(function(resolve) {
        axios({
          method: 'get',
          url: '/sys/dictionary/public-dict-combo',
          params: {tableType: that.type, isPublic: that.isPublic}
        }).then(function (res) { 
          that.orgType = res.result
          resolve(that.orgType)
        })
      })
    },
    documentData() {
      for(const key in this.orgType) {
        if(this.$t('dct.' + this.type + key) != 'dct.' + this.type + key){
          this.orgType [key]= this.$t('dct.' + this.type + key)
        }
      }
      return this.orgType
    }
  },
}
</script>