<template>
  <div>
    <div>
      <a-card style="background-color:#fff8db;margin-bottom:10px" v-show="show">
        <div class="table-page-search-wrapper">
          <a-form class="ant-advanced-search-form" layout="inline">
            <a-row :gutter="32">
              <a-col :xl="8" :md="7" :sm="48">
                <a-form-item :label="$t('dataedit.date')" >
                  <pick-range :defaultValue="stratRange" showLevel="1" @change="onChangeTime" />
                </a-form-item>
              </a-col>
              <a-col :xl="8" :md="6" :sm="48">
                <a-form-item :label="$t('dataedit.device_num')" >
                  <a-input-search v-model="queryParam.deviceNum" :placeholder="$t('dataedit.device_num_placeholder')" @search="SelectDev" />
                </a-form-item>
              </a-col>
              <a-col :xl="5" :md="5" :sm="48">
                <span>
                  <btn-search @click="queryData" :showInput="false"></btn-search>&nbsp; &nbsp;
                </span>
              </a-col>
            </a-row>
          </a-form>
        </div>
      </a-card>
      <a-card>
        <div class="table-page-search-wrapper" style="margin-bottom: 10px;">
          <a-row>
            <a-col :md="19" :sm="19">
            </a-col>
            <a-col :md="5" :sm="5">
              <sorter @refresh="$refs.table.refresh()" @show="showSearch"></sorter>
            </a-col>
          </a-row>
        </div>
        <s-table 
          ref="table" 
          rowKey="dataType,id"
          :data="loadData" 
          :bordered="false" 
        > 
          <a-table-column 
            :title="$t('task.index1')"
            width="50px"
            align="center"
          >
            <span slot-scope="text">
              <a-icon type="schedule" theme="twoTone" class="icon" style="font-size:25px"/>
            </span>
          </a-table-column>
          <a-table-column
            :title="this.$t('dataedit.index1')"
            width="150px"
            dataIndex="veeType"
            :filters="filters1"
            :filterMultiple="false"
            @FilterDropdownVisibleChange="onChange(1)"
          >
            <span slot-scope="text,record">
              <a-button type="link" @click="showField(record)" v-if="record.veeType==0&&record.veeResult==1">{{ $t('dataedit.result_type0') }}</a-button>
              <a-button type="link" @click="showField(record)" v-if="record.veeType==0&&record.veeResult==2">{{ $t('dataedit.result_type1') }}</a-button>
              <a-button type="link" @click="showField(record)" v-if="record.veeType==1&&record.veeResult==2">{{ $t('dataedit.result_type2') }}</a-button>
              <a-button type="link" @click="showField(record)" v-if="record.veeType==1&&record.veeResult==0">{{ $t('dataedit.result_type3') }}</a-button>
              <a-button type="link" @click="showField(record)" v-if="record.veeType==2&&record.veeResult==0">{{ $t('dataedit.result_type4') }}</a-button>
            </span>
          </a-table-column>
          <a-table-column
            :title="this.$t('vee.datatype')"
            width="150px"
            dataIndex="veeDataType"
            :filters="filters2"
            :filterMultiple="false"
            @FilterDropdownVisibleChange="onChange(2)"
          >
            <span slot-scope="text,record">
              <span>{{ $t('vee.ftype'+record.veeDataType) }}</span>
            </span>
          </a-table-column>
          <a-table-column 
            :title="$t('dataedit.device_num')"
            data-index="deviceNum"
            width="150px"
          >
          </a-table-column>
          <a-table-column 
            :title="$t('device.index2')"
            width="150px"
            data-index="deviceDescription"
          >
          </a-table-column>
          <a-table-column
            :title="this.$t('device.index4')"
            width="150px"
            dataIndex="meterSort"
            :filters="filters3"
            :filterMultiple="false"
            @FilterDropdownVisibleChange="onChange(3)"
          >
            <span slot-scope="text,record">
              <span v-if="record.meterSort==3">{{ $t('dct.METER_SORT3') }}</span>
              <span v-if="record.meterSort==1">{{ $t('dct.METER_SORT1') }}</span>
              <span v-if="record.meterSort==2">{{ $t('dct.METER_SORT2') }}</span>
            </span>
          </a-table-column>
          <a-table-column 
            :title="$t('dataedit.date')"
            width="200px"
            data-index="readTime"
          >
            <span slot-scope="text,record">
              <pick-label :defaultValue="record.readTime" />
            </span>
          </a-table-column>
          <a-table-column 
            :title="$t('vee.files')"
            width="150px"
            data-index="fieldDescription"
          >
          </a-table-column>
          <a-table-column 
            :title="$t('dataedit.index2')"
            width="100px"
            data-index="realValue"
          ></a-table-column>
          <a-table-column 
            :title="$t('dataedit.index3')"
            width="100px"
            data-index="estimateValue"
          ></a-table-column>
          <a-table-column 
            :title="$t('dataedit.index4')"
            width="100px"
            data-index="editValue"
          ></a-table-column>
          <a-table-column
            :title="this.$t('task.action')"
            dataIndex="action"
          > 
            <span slot-scope="text,record">
              <btn-edit @click="onUpdate(record)" type="a" v-if="record.veeType!=2"></btn-edit>
            </span>
          </a-table-column>
        </s-table>
      </a-card>
    </div>
    <search-meter ref="selectMeter" @ok="loadDeviceData" :multiple="false" :params="{}"></search-meter>
    <edit-data ref="editData" @ok="reflshData" :params="{}"></edit-data>
    <show-details ref="showDetails" ></show-details>
    <dict type="METER_SORT" table="params" ref="meterSort" v-show="false" />
  </div>
</template>
<script>
  import STable from '@/components/table/'
  import { axios } from '@/utils/request'
  import Dict from '@/views/public/Dict'
  import EditData from './EditData'
  import ShowDetails from './ShowDetails'
  import { toUTC } from '@/inhe/picker/utils'
  import SearchMeter from '@/views/public/SearchMeter'

  export default {
    name: 'Index',
    components: {
      STable,
      Dict,
      SearchMeter,
      EditData,
      ShowDetails
    },
    data() {
      return {
        show:true,
        queryParam: {deviceNum:''},
        startTime:toUTC(new Date(new Date().getFullYear()+'-'+(new Date().getMonth()+1))),
        endTime: toUTC(new Date()),
        stratRange:[toUTC(new Date(new Date().getFullYear()+'-'+(new Date().getMonth()+1))),toUTC(new Date())],
        filters1: [{text: this.$t('dataedit.result_type0'),value: '0'},{text: this.$t('dataedit.result_type1'),value: '1'},{text: this.$t('dataedit.result_type2'),value: '2'},{text: this.$t('dataedit.result_type3'),value: '3'},{text: this.$t('dataedit.result_type4'),value: '4'}],
        filters2: [{text: this.$t('vee.ftype05'),value: '05'},{text: this.$t('vee.ftype06'),value: '06'},{text: this.$t('vee.ftype07'),value: '07'},{text: this.$t('vee.ftype09'),value: '09'},{text: this.$t('vee.ftype10'),value: '10'}],
        filters3: [{text: 'none',value: '0'}],
        loadData:  parameter => {
          parameter.startTime = this.startTime
          parameter.endTime = this.endTime
          parameter.meterSort instanceof Array?this.queryParam.meterSort = parameter.meterSort[0]:'' 
          parameter.veeType instanceof Array?this.queryParam.veeType = parameter.veeType[0]:'' 
          parameter.veeDataType instanceof Array?this.queryParam.veeDataType = parameter.veeDataType[0]:'' 
         
          return axios({
            url: '/vee/dataedit/search',
            method: 'get',
            params: Object.assign(parameter, this.queryParam)
          }).then(res => {           
            return res.result
          })
        },

      }
    },
    methods: {
      showField(record){
         this.$refs.showDetails.showModal(record)
      },
      SelectDev(){
         this.$refs.selectMeter.showModal()
      },
      loadDeviceData(item){console.log(item)
          this.queryParam.deviceNum=item[0].deviceNum
      },
      showSearch() {
        this.show = !this.show
      },
      onUpdate(record){
         this.$refs.editData.showModal(record)
      },
      reflshData(){
        this.$refs.table.refresh()
      },
      //输入任务名称的search,上方列表表刷新
      queryData(){
        this.$refs.table.refresh()
      },
       //注册时间范围选择
      onChangeTime(value){
        value instanceof Array?this.startTime=value[0]:this.startTime=''
        value instanceof Array?this.endTime=value[1]:this.endTime=''
      },
      onChange(type){
       if(type==3){
         this.filters3=this.$refs.meterSort.getAll()
       }
      },
    }
  }
</script>
<style lang="scss" scoped>

</style>